import Sortable from 'sortablejs'

export default function(el: Element, list: any[], newList: any[]) {
  Sortable.create(el, {
    ghostClass: 'sortable-ghost', // Class name for the drop placeholder,
    handle: '.sortable-drag',
    setData: function(dataTransfer: any) {
      dataTransfer.setData('Text', '')
      // to avoid Firefox bug
      // Detail see : https://github.com/RubaXa/Sortable/issues/1012
    },
    onEnd: (evt: any) => {
      const targetRow = list.splice(evt.oldIndex, 1)[0]
      list.splice(evt.newIndex, 0, targetRow)

      // for show the changes, you can delete in you code
      const tempIndex = newList.splice(evt.oldIndex, 1)[0]
      newList.splice(evt.newIndex, 0, tempIndex)
    }
  })
}
